import React from 'react'
import { SiGithub } from 'react-icons/si';
import { SiKaggle } from 'react-icons/si';
import { SiGmail } from 'react-icons/si';
import './css/NavLinks.css'

import {
  Link
} from 'react-router-dom'

const NavLinks = () => {
  return (
    <nav className="nav">
      <Link className="nav__title" to="/">Dan Islamov</Link>
      <ul className="nav__icons ">
        <li><a className="nav__icon github" href="https://github.com/sleepwallking"><SiGithub/></a></li>
        <li><a className="nav__icon kaggle" href="https://www.kaggle.com/sleepwalkin"><SiKaggle/></a></li>
        <li><a className="nav__icon mail" href="islamovdan.ds@gmail.com"><SiGmail/></a></li>
      </ul>
    </nav>
  )
}

export default NavLinks