import React from 'react'
import { Parallax } from 'react-parallax';

import './css/Home.css'

const image1 = "https://imagens.publico.pt/imagens.aspx/1574515?tp=UH&db=IMAGENS&type=GIF"
const image2 = "https://maiboroda.ru/wp-content/uploads/2019/03/clement-h-544786-unsplash.jpg"
const image3 = "https://archello.s3.eu-central-1.amazonaws.com/images/2013/12/04/HaroldWay28.1506578690.2497.jpg"

const Home = () => {
  return (
    <div className="wrapper">

      <Parallax bgImage={image1} strength={500}>
        <div className="parallax">
          <h1 className="h1">Junior Data Scientist</h1>
          <h2 className="h2">Machine Learning | Computer vision | Deep Learning</h2>
        </div>
      </Parallax>

      <div className="text">
        <p className="text__p">
          Меня зовут Даниил Исламов и я начинающий специалист в сфере Data Science.
        </p>
      </div>

      <div className='skills'>
          <div className='div'>
            <p className='p'>Hard Skills</p>
            <ul className='ul'>
              <li className='li'>Python и его библиотеки: Sklearn, Seaborn, Tensorflow и другие</li>
              <li className='li'>Основы статистики: Теория вероятностей, Теорема Байеса и тд.</li>
              <li className='li'>Умение работать с реляционными базами данных: PostgreSQL, MySQL</li>
              <li className='li'>Навыки сбора и обработки данных: Selenium, Pandas</li>
              <li className='li'>Знание основных алгоритмов машинного обучения: Линейная регрессия, Дерево решений, Решающий лес и прочие</li>
            </ul>
          </div>
          <div className='div'>
            <p className='p'>Soft Skills</p>
            <ul className='ul'>
              <li className='li'>Внимательность к деталям</li>
              <li className='li'>Креативность в нахождении решений</li>
              <li className='li'>Системный подход к решению задач</li>
              <li className='li'>Основы тайм-менеджмента</li>
              <li className='li'>Нацеленность на результат</li>
            </ul>
          </div>
      </div>
      <Parallax bgImage={image2} strength={500}>
        <div className="parallax">
          <h1 className="h1">Pet Проекты</h1>
          <h2 className="h2"></h2>
        </div>
      </Parallax>
      <div className="cards">
        <div className='card'>
        <img src=
          "https://raw.githubusercontent.com/sleepwallking/hand-tracking-project/main/volume-control.gif"
          className="card__img" />
        <a className='card__name' href='https://github.com/sleepwallking/hand-tracking-project'>Hand Tracking</a>
        <p className='card__descrip'> Регулирование громкости звука на компьютере. Используются библиотеки <a className='link' href="https://opencv.org/">OpenCV</a> и <a className='link' href="https://mediapipe.dev/">MediaPipe</a>.</p>
        </div>
        <div className='card'>
        <img src=
          "https://github.com/sleepwallking/pose-estimination/blob/main/readme.gif?raw=true"
          className="card__img" />
        <a className='card__name' href='https://github.com/sleepwallking/pose-estimination'>Pose Estimination</a>
        <p className='card__descrip'>Отслеживание движений человека на видео. Используется библиотека <a className='link' href="https://www.tensorflow.org/hub/tutorials/movenet">MoveNet</a> от Tensorflow Lite.</p>
        </div>
        <div className='card'>
        <img src=
         "https://github.com/sleepwallking/object-detection/blob/main/readme.gif?raw=true"
          className="card__img" />
        <a className='card__name' href='https://github.com/sleepwallking/object-detection'>Object Detection</a>
        <p className='card__descrip'>Распознавание объектов на видео. Испоьзуются библиотеки <a className='link' href="https://www.tensorflow.org/api_docs/python/tf/keras/applications/mobilenet/MobileNet">MobileNet</a> и <a className='link' href="https://opencv.org/">OpenCV</a>.</p>
        </div>
      </div>
      <Parallax bgImage={image3} strength={500}>
        <div className="parallax">
        </div>
      </Parallax>
    </div>
  )
}

export default Home
