import React from 'react'
import './App.css'

import {
  BrowserRouter,
  Switch,
  Route,
  Redirect
} from 'react-router-dom'

import Header from './Components/Header/Header'
import Footer from './Components/Footer/Footer'
import Home from './Components/Home/Home'

const App = () => {
  return (
    <BrowserRouter>
      <Header />
      <Switch>
        <Route exact path="/" component={Home} />
        <Redirect to='/' />
      </Switch>
      <Footer />
    </BrowserRouter>
  )
}

export default App